<template>
  <div :class="{'open':ifSort || ifscreen,'filterView':true}" @click.self="hidenView">
    	<nav  class="navTab">
    		<div v-for="(item,index) in filterData.navTab" :key="index" @click="filterSort(index)" :class="{'blodfz':currentFilter == index}">
    			<span>{{item.name}}</span>
    			<i :class="'fa fa-'+item.icon" v-if="item.icon"></i>
    		</div>
    	</nav>
    	<div class="sortBy" v-if="ifSort">
    		<ul>
    			<li v-for="(item,index) in filterData.sortBy" :key="index" @click="setSort(item,index)" :class="{'selectName':index == currentSort}">
    				<span>{{item.name}}</span>
    				<i class="fa fa-check" v-if="index == currentSort"></i>
    			</li>
    		</ul>
    	</div>
    	<div class="screenBy" v-if="ifscreen">
    		<ul v-for="(item,index) in filterData.screenBy" :key="index">
    			<section>{{item.title}}</section>
    			<li v-for="(value,i) in item.data" :key="i" :class="{'pitch_on':value.select}" @click="pitchOn(item,value)">
    				<img :src="value.icon" alt="" v-if="value.icon">
    				{{value.name}}
    			</li>
    		</ul>
    		<div>
    			<button class="qx" @click="cancel">消除</button>		
    			<button class="srceenOk" @click="srceenOk">确定</button>		
    		</div>
    	</div>
    </div>
</template>

<script>
export default {
  name: 'filterView',
  data(){
  	return{
  		ifSort:false,
  		ifscreen:false,
  		currentFilter:0,
  		currentSort:0
  	}
  },
  props: {
    filterData:Object
  },
  methods:{
  	filterSort(index){
  		switch(index){
  			case 0:
  				this.ifSort = true;
  				this.ifscreen = false;
  				this.$emit('openSort',this.ifSort);
  				this.currentFilter = index;
  				break;
  			case 1:
  				this.currentFilter = index;
  				this.hidenView();
  				break;
  			case 2:
  				this.currentFilter = index;
  				this.hidenView();
  				break;
  		  	case 3:
  				this.currentFilter = index;
  				this.ifSort = false;
  				this.ifscreen = true;
  				this.$emit('openSort',this.ifscreen);
  				break;
  			default:
  				this.ifSort = false;
  		}
  	},
  	hidenView(){
  		this.ifSort = false;
		this.ifscreen = false;
		this.$emit('openSort',this.ifSort);
  	},

  	setSort(item,index){
  		this.filterData.navTab[0].name = item.name;
  		this.currentSort = index;
  		this.ifSort = false;
  		this.$emit('openSort',this.ifSort);
      this.$emit('updata',{ condition : item.code });
  	},
  	pitchOn(item,value){
  		// 是否支持多选
  		if(item.id == 'MPI'){
  			value.select = !value.select;
  		}else{
  			item.data.forEach(option=>{
  				option.select = false;
  			});
  			value.select = !value.select;
  		}
  	},
  	cancel(){
  		this.filterData.screenBy.forEach(i=>{
  			i.data.forEach(item=>{
  				item.select = false;
  			})
  		})
  	},
  	srceenOk(){
      let obj = {
        MPI:'',
        offer:'',
        per:''
      };
      let mpi = '';
      this.filterData.screenBy.forEach(i=>{
        i.data.forEach(item=>{
          if(item.select == true){
            if(i.id=="MPI"){
              mpi += item.code +',';
              obj[i.id]=mpi;
            }else{
              obj[i.id] = item.code;
            }
          }
        });
      });
      console.log(obj);
      this.$emit('updata',{ condition : obj });
      this.hidenView();
      
  	}
  }
}
</script>


<style scoped>
.filterView{
	margin-top: 0.4rem;
	background-color: none;
	transition: all 0.3s;
}
.open{
	width: 100%;
	height: 100%;
	position: fixed;
	top:0;
	left: 0;
	margin: 0!important;
	background-color: rgba(0,0,0,0.5);
}
.open>.navTab{
	margin-top:calc(1rem + 20px) ;
}
.navTab{
	width: 100%;
	height: 1rem;
	background-color: #fff;
	display: flex;
	padding: 5px 0;
	flex-direction: row;
	border-bottom: 1px solid #ccc;
	font-size: 0.5rem;
}
.navTab>div{
	width: 25%;
	height: 100%;
	text-align: center;
	line-height: 1rem;
	box-sizing: border-box;
	color: #878787;
}
.blodfz{
	font-weight: bold;
	color: #4e4e4e!important;
}
.sortBy>ul>li{
	width: 100%;
	padding: 0 10px;
	box-sizing: border-box;
	height: 1.8rem;
	font-size: 0.6rem;
	background-color: #fff;
	display: flex;
	color: #878787;
	border-bottom: 1px solid #dedede;
	justify-content: space-between;
	list-style: none;
	align-items: center;
}

.selectName{
	color: #00dfd6!important;
}
.screenBy{
	background-color: #fff;
}
.screenBy ul{
	display: flex;
	justify-content: flex-start;
	flex-wrap: wrap;
}
.screenBy ul section{
	width: 100%;
	line-height: 1.3rem;
	height: 1.3rem;
}
.screenBy ul li{
	width: 4rem;
	height: 1rem;
	margin:5px 10px;
	list-style: none;
	background-color: #f0f0f0;
	font-size: 0.5rem;
	color: #565656;
	border-radius: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
}
.screenBy ul li img{
	width: 0.6rem;
	height: 0.6rem;
	margin: 0 3px;
}
.qx{
	margin-top: 0.6rem;
	width: 50%;
	height: 2rem;
	line-height: 2rem;
	border:none;
}
.srceenOk{
	margin-top: 0.6rem;
	width: 50%;
	height: 2rem;
	line-height: 2rem;
	border:none;
	background-color: #22e222;
	color: #fff;
}
.pitch_on{
	background-color: #d2ffff!important;
	color: #1ba0b1!important;
	border:1px solid #1ba0b1;
	box-sizing: border-box;
}
</style>