<template>
  <div class="Reting">
      <i v-for="(item,index) in grades" :key="index" :class="'fa fa-'+item"></i>
   </div>
</template>

<script>
export default {
  name: 'Reting',
  props: {
    grade: Number
  },
  computed:{
  	grades(){
  		let obj = [];
  		const LENGTH = 5;
  		const ON = 'star';
  		const OFF = 'star-o';
  		const HALF = 'star-half-empty';
  		let grade = Math.floor(this.grade*2)/2;
  		for(let i = 0;i < Math.floor(grade);i++){
  			obj.push(ON);
  		}
  		if((grade*2)%2 == 1){
  			obj.push(HALF);
  		}
  		while(obj.length < LENGTH){
  			obj.push(OFF);
  		}
  		return obj;
  	}
  }
}
</script>


<style scoped>
.Reting{
	color: #ffeb3b;
}
</style>
