<template>
  <div class="home">
   	<div class="location">	
   		<router-link tag='p' :to="{name:'address'} ":nowress='ress'><i class="fa fa-map-marker"></i>{{getlocation}}</router-link>
   	</div>
    <div :class="{'show':true,'openfliter':if_Sort}">
      <input type="text" placeholder='请输入搜索内容'>
    </div>
    <div class="context">
    <div class="swipe"> 
     <mt-swipe :auto="4000" class="context">
        <mt-swipe-item v-for="(item,index) in swipeImgs" :key="index">
          <img :src="item" alt="">
          <!-- {{item}} -->
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="entries">
      <mt-swipe :auto="0">
        <mt-swipe-item v-for="(item,index) in entries" :key="index" class="entries_warp">
          <div v-for="(entri,i) in item" :key="i">
            <div class="entries_img"><img :src="entri.image" alt=""></div>
            <p>{{entri.name}}</p>
          </div>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="tisj">——推荐商家——</div>
    <FilterView :filterData="filterData" @openSort='openSort' @updata="updata"></FilterView>
    </div>
    
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
      <section v-infinite-scroll="loadMore" infinite-scroll-disabled="loading" infinite-scroll-distance="10">
        <Merchant v-for="(item,index) in restaurant" :key="index" :restaurant="item.restaurant"></Merchant>
      </section>
    </mt-loadmore>
    </div>
</template>

<script>
import { Swipe, SwipeItem } from 'mint-ui';
import { Loadmore } from 'mint-ui';
import FilterView from './../components/filterView'
import Merchant from './../components/Merchant'
import { InfiniteScroll } from 'mint-ui';
import { Toast } from 'mint-ui';

export default {
  name: 'Home',
  data(){
  	return{
  		ress:'',
      swipeImgs:[],
      entries:[],
      filterData:{},
      if_Sort:false,
      restaurant:[],
      allLoaded:false,
      page:1,
      num:5,
      ifNull:false,
      data:null
  	}
  },
  computed:{
  	getlocation(){
  		this.ress = this.$store.getters.address;
  		return this.$store.getters.address;
  	}
  },
  created(){
    this.getData();
  },
  methods:{
  	getData(){
      this.$axios.get("/api/profile/shopping").then(res=>{
        this.swipeImgs = res.data.swipeImgs;
        this.entries = res.data.entries;
      });
      this.$axios.get("/api/profile/filter").then(res=>{
        // console.log(res.data);
        this.filterData = res.data;
      });
      this.getData2();
    },
    getData2(){
      this.$axios.post(`/api/profile/restaurants/${this.page}/${this.num}`,this.data).then(res=>{
        console.log(res.data);
        this.restaurant = res.data;
      });
    },
    openSort(ifSort){
      this.if_Sort = ifSort;
    },
    loadTop() {
      this.page = 1;
      this.ifNull = false;
      this.getData2();
      this.$refs.loadmore.onTopLoaded();
    },
    loadBottom() {
      this.allLoaded = true;
      this.$refs.loadmore.onBottomLoaded();
      // this.allLoaded = true;// 若数据已全部获取完毕
    },
    loadMore(){
      // console.log(1);
      this.page++;
      if(this.ifNull){
        Toast('没有更多信息了哦');
      }else{
        this.$axios.post(`/api/profile/restaurants/${this.page}/${this.num}`).then(res=>{
          res.data.forEach(item=>{
            this.restaurant.push(item);
          })
          if(res.data.length < 5){
            this.ifNull = true;
          }

          // this.restaurant = res.data;
        });
      }

    },
    updata(condation){
      this.data = condation;
      console.log(condation);
      this.getData2();
    }
  },
  components:{
    'Swipe.name': Swipe,
    'Loadmore.name':Loadmore,
    'SwipeItem.name': SwipeItem,
    FilterView,
    Merchant,
    InfiniteScroll
  }
}
</script>


<style scoped>
.home{
  width: 100%;
  height: 100%;
  overflow:auto;
}
.location{
	background-color: #00dfd6;
	height: 1rem;
  line-height: 1rem;
}
.show{
  background-color: #00dfd6;
  height: 1rem;
  padding: 10px 0px;
  position: sticky;
  top: 0px;
  left: 0px;
  z-index: 999;
  width: 100%;
  text-align:center;
  /*box-sizing: border-box;*/
}
.openfliter{
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
}
.show input{
  width: 95%;
  height: 100%;
  border-radius: 5px;
  border: none;
  padding-left: 1em;
  font-size: 0.6rem;
  line-height: 1rem;
  box-sizing: border-box;
}
.location p{
	overflow: hidden;
	height: 30px;
	width: 100%;
	line-height: 30px;
	box-sizing: border-box;
	color: #fff;
}
.location p i{
	padding: 0 10px;
	display: inline-block;
}
.context{
  background-color: #fff;
}
.swipe{
  width: 100%;
  height: 3.7rem;
}
.swipe img{
  width: 100%;
  height: auto;
}
.entries{
  width: 100%;
  height: 6rem;
  background-color: #fff;
  padding: 1vw;
  margin: 2vw 0;
  box-sizing: border-box;
}
.entries_warp{
  display: flex !important;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;

}
.entries_warp>div{
  width: 2.8rem;
  height: 3rem;
  text-align: center;
  font-size: 0.5rem;
}
.entries_img img{
  width: 1.6rem;
  height: 1.6rem;
}
.tisj{
  text-align: center;
  font-size:16px;
  color: #878787;
}
</style>
